@import url(//db.onlinewebfonts.com/c/ffea4728b1a325151a1c589e20a992f0?family=Papyrus);
* {
  font-family: Papyrus, Helvetica, sans-serif;
  margin: auto;
  overflow-x: hidden;
}

#background-image {
  height: 50px;
  background-repeat: no-repeat;
  background-position: 0% 15%;
  background-size: cover;
  background-image: url(/memory-card/static/media/416642.ff16af5a.jpg);
  filter: blur(3px);
  -webkit-filter: blur(3px);
  height: 150px;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

#title {
  position: absolute;
  top: 50px;
  left: 20px;
  color: #121212;
  padding: 10px;
  border-radius: 20px;
  width: 200px;
}

#scoreboard {
  position: absolute;
  top: 50px;
  right: 30px;
  color: #121212;
  background-color: white;
  padding: 10px;
  border-radius: 20px;
  width: 200px;
  font-size: 20px;
}

#cardwrapper {
  width: 80%;
}

#gamebackground {
  height: 100%;
  width: 100%;
  position: absolute;
  bottom: 0px;
  background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 1),
      rgba(255, 255, 255, 0)
    ),
    url("https://cdn.vox-cdn.com/thumbor/r92uf-e9ZWAwI8ircAF4NeDm2Fg=/0x0:1285x717/920x613/filters:focal(616x405:820x609):format(webp)/cdn.vox-cdn.com/uploads/chorus_image/image/61448953/Screen_Shot_2018_09_18_at_12.03.02_PM.0.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  z-index: -1;
}

.card {
  display: inline-block;
  cursor: pointer;
  margin: 15px;
  border-radius: 20px;
  padding: 10px;
  transition: 0.2s ease;
  color: white;
  text-align: center;
}

