.cardimage {
  min-height: 150px;
  min-width: 150px;
  background-size: cover;
  border-radius: 20px;
  margin-bottom: 5px;
}

.card:hover {
  transform: scale(1.1);
  transform-origin: center;
  box-shadow: 0px 0px 10px gray;
}

.air {
  background-color: rgb(223, 224, 224);
  color: #121212;
}

.water {
  background-color: rgb(131, 131, 255);
}

.earth {
  background-color: rgb(231, 174, 100);
}

.fire {
  background-color: rgb(255, 0, 0);
}

footer {
  position: absolute;
  width: 100%;
  bottom: 0px;
  padding: 20px;
  text-align: center;
}

footer p {
  background-color: white;
  padding: 5px;
  border-radius: 30px;
  width: 150px;
}

footer a {
  text-decoration: none;
  color: #121212;
}

footer a:hover {
  text-decoration: underline;
}

